import { searchTrack } from '@/api/tracks'

export default {
  namespaced: true,
  state: {
    tracks: [],
    favorites: []
  },
  mutations: {
    SET_TRACKS (state, tracks) {
      state.tracks = tracks
    },
    ADD_FAV (state, track) {
      state.favorites.push(track)
    }
  },
  actions: {
    async searchTrack (context, query) {
      const res = await searchTrack(query)
      console.log(res)
      context.commit('SET_TRACKS', res.data.tracks.items)
    }
  }
}
