<template>
  <div class="about">
    <v-row align="center" justify="center">
      <v-col cols="12" sm="12" lg="8">
        <v-text-field
          filled
          hide-details
          rounded
          append-icon="mdi-magnify"
          label="Search songs..."
          v-model="query"
          @input="search()"
        >
        </v-text-field>
      </v-col>
    </v-row>
    <v-row>
      <v-col v-for="item in tracks" :key="item.id" cols="12" xs="12" md="6" lg="4">
        <TrackCard :track="item" />
      </v-col>
    </v-row>
  </div>
</template>

<script>
import TrackCard from '@/components/TrackCard'
export default {
  data() {
    return {
      query: 'rock'
    }
  },
  components: {
    TrackCard
  },
  created() {
    this.search()
  },
  computed: {
    tracks() {
      return this.$store.state.tracks.tracks
    }
  },
  methods: {
    async search() {
      this.$store.dispatch('tracks/searchTrack', this.query)
    }
  }
}
</script>